import React, { useState } from 'react';
import { useQuery, useMutation, gql } from '@apollo/client';
import {
  Modal,
  TextField,
  CircularProgress,
  Select,
  MenuItem,
  Box,
  Typography,
  Button,
  Snackbar,
  Alert,
} from '@mui/material';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import 'tailwindcss/tailwind.css';

const theme = createTheme({
  palette: {
    primary: {
      main: '#9c27b0', // purple
    },
    secondary: {
      main: '#9e9e9e', // grey
    },
  },
});

const GET_POSTS = gql`
  query GetPosts {
    findAllPosts {
      id
      imageHashes
      likes
      user {
        id
        username
        followers
        followings
      }
    }
  }
`;

const GET_USERS = gql`
  query GetUsers {
    findAll {
      id
      username
      followers
      followings
      posts {
        id
        imageHashes
        likes
      }
    }
  }
`;

const LIKE_POST = gql`
  mutation LikePost($id: Int!) {
    likePost(id: $id) {
      id
      likes
    }
  }
`;

const CREATE_POST = gql`
  mutation CreatePost($input: CreatePostInput!) {
    createPost(createPostInput: $input) {
      id
      imageHashes
      likes
      user {
        id
        username
        followers
        followings
      }
    }
  }
`;

const CREATE_USER = gql`
  mutation CreateUser($input: CreateUserInput!) {
    createUser(createUserInput: $input) {
      id
      username
      followers
      followings
      posts {
        id
        imageHashes
        likes
      }
    }
  }
`;

function App() {
  const { loading, error, data: postsData, refetch } = useQuery(GET_POSTS);
  const { data: usersData } = useQuery(GET_USERS);
  const [likePost] = useMutation(LIKE_POST, {
    onCompleted: () => {
      refetch();
    }
  });
  const [createPost] = useMutation(CREATE_POST, {
    onCompleted: () => {
      setSnackbarMessage('Post created successfully!');
      setSnackbarOpen(true);
    }
  });
  const [createUser] = useMutation(CREATE_USER, {
    onCompleted: () => {
      setSnackbarMessage('User created successfully!');
      setSnackbarOpen(true);
    }
  });
  const [postModalOpen, setPostModalOpen] = useState(false);
  const [userModalOpen, setUserModalOpen] = useState(false);
  const [postFormData, setPostFormData] = useState({ userId: '', imageBase64Strings: [], content: '' });
  const [userFormData, setUserFormData] = useState({ username: '', followers: 0, followings: 0 });
  const [snackbarOpen, setSnackbarOpen] = useState(false);
  const [snackbarMessage, setSnackbarMessage] = useState('');

  const handlePostFormChange = (e) => {
    setPostFormData({ ...postFormData, [e.target.name]: e.target.value });
  };

  const handleUserFormChange = (e) => {
    setUserFormData({ ...userFormData, [e.target.name]: e.target.value });
  };

  const handlePostSubmit = (e) => {
    e.preventDefault();
    createPost({ variables: { input: { ...postFormData, userId: parseInt(postFormData.userId) } } });
    setPostModalOpen(false);
    setPostFormData({ userId: '', imageBase64Strings: [], content: '' });
  };

  const handleUserSubmit = (e) => {
    e.preventDefault();
    createUser({ variables: { input: { ...userFormData, followers: parseInt(userFormData.followers), followings: parseInt(userFormData.followings) } } });
    setUserModalOpen(false);
    setUserFormData({ username: '', followers: 0, followings: 0 });
  };

  const handleImageChange = (e) => {
    const files = Array.from(e.target.files).slice(0, 3);
    Promise.all(files.map(fileToBase64))
      .then((base64Strings) => {
        setPostFormData({ ...postFormData, imageBase64Strings: base64Strings });
      })
      .catch((err) => console.error(err));
  };

  const fileToBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result);
      reader.onerror = (error) => reject(error);
    });
  };

  const handleCloseSnackbar = () => {
    setSnackbarOpen(false);
  };

  if (loading) return <CircularProgress />;
  if (error) return <p>Error :(</p>;

  return (
    <ThemeProvider theme={theme}>
      <div className="App p-4">
        <Box mt={5} display="flex" justifyContent="center">
          <Button
            onClick={() => setPostModalOpen(true)}
            className="mr-2"
            variant="contained"
            color="secondary"
          >
            Add Post
          </Button>
          <Button
            onClick={() => setUserModalOpen(true)}
            variant="contained"
            color="secondary"
          >
            Add User
          </Button>
        </Box>
        {postsData && postsData.findAllPosts && postsData.findAllPosts.length > 0 ? (
          postsData.findAllPosts.map(({ id, imageHashes, likes, user }) => (
            <div key={id} className="mb-4 p-4 bg-white shadow rounded">
              <Typography variant="h6">{user.username}</Typography>
              <p>
                Followers: {user.followers} | Following: {user.followings}
              </p>
              <p>Likes: {likes}</p>
              <Button
                onClick={() => likePost({ variables: { id } })}
                className="mr-2"
                variant="contained"
                color="primary"
              >
                Like
              </Button>
              <Box display="flex" justifyContent="center" mt={2}>
                {imageHashes.map((hash, index) => (
                  <img
                    key={index}
                    src={"http://localhost:3000/image/" + hash}
                    alt={`Post ${index + 1}`}
                    style={{ width: '100px', height: '100px', margin: '0 10px' }}
                  />
                ))}
              </Box>
            </div>
          ))
        ) : (
          <Box display="flex" flexDirection="column" justifyContent="center" alignItems="center" height="100vh">
            <Typography variant="h6" color="textSecondary" gutterBottom>
              Nothing for you here!
            </Typography>
          </Box>
        )}

        <Modal open={postModalOpen} onClose={() => setPostModalOpen(false)}>
          <Box
            sx={{
              position: 'absolute',
              top: '50%',
              left: '50%',
              transform: 'translate(-50%, -50%)',
              bgcolor: 'background.paper',
              boxShadow: 24,
              p: 4,
              width: 400
            }}
          >
            <Typography variant="h6" gutterBottom>
              Add Post
            </Typography>
            <form onSubmit={handlePostSubmit}>
              <Select
                name="userId"
                value={postFormData.userId}
                onChange={handlePostFormChange}
                fullWidth
                required
                margin="normal"
                displayEmpty
              >
                <MenuItem value="">Select User</MenuItem>
                {usersData && usersData.findAll && usersData.findAll.map((user) => (
                  <MenuItem key={user.id} value={user.id}>
                    {user.username}
                  </MenuItem>
                ))}
              </Select>
              <Button
                variant="contained"
                component="label"
                fullWidth
                margin="normal"
              >
                Add Images
                <input
                  type="file"
                  hidden
                  accept="image/*"
                  multiple
                  onChange={handleImageChange}
                />
              </Button>
              <Box display="flex" justifyContent="center" mt={2}>
                {postFormData.imageBase64Strings.map((hash, index) => (
                  <img
                    key={index}
                    src={hash}
                    alt={`Post ${index + 1}`}
                    style={{ width: '100px', height: '100px', margin: '0 10px' }}
                  />
                ))}
              </Box>
              <TextField
                name="content"
                label="Content"
                value={postFormData.content}
                onChange={handlePostFormChange}
                fullWidth
                required
                margin="normal"
                multiline
              />
              <Button
                variant="contained"
                color="primary"
                type="submit"
                fullWidth
              >
                Submit
              </Button>
            </form>
          </Box>
        </Modal>

        <Modal open={userModalOpen} onClose={() => setUserModalOpen(false)}>
          <Box
            sx={{
              position: 'absolute',
              top: '50%',
              left: '50%',
              transform: 'translate(-50%, -50%)',
              bgcolor: 'background.paper',
              boxShadow: 24,
              p: 4,
              width: 400
            }}
          >
            <Typography variant="h6" gutterBottom>
              Add User
            </Typography>
            <form onSubmit={handleUserSubmit}>
              <TextField
                name="username"
                label="Username"
                value={userFormData.username}
                onChange={handleUserFormChange}
                fullWidth
                required
                margin="normal"
              />
              <TextField
                name="followers"
                label="Followers"
                value={userFormData.followers}
                onChange={handleUserFormChange}
                fullWidth
                required
                margin="normal"
                type="number"
              />
              <TextField
                name="followings"
                label="Followings"
                value={userFormData.followings}
                onChange={handleUserFormChange}
                fullWidth
                required
                margin="normal"
                type="number"
              />
              <Button
                variant="contained"
                color="primary"
                type="submit"
                fullWidth
              >
                Submit
              </Button>
            </form>
          </Box>
        </Modal>
        
        <Snackbar open={snackbarOpen} autoHideDuration={6000} onClose={handleCloseSnackbar}>
          <Alert onClose={handleCloseSnackbar} severity="success" sx={{ width: '100%' }}>
            {snackbarMessage}
          </Alert>
        </Snackbar>
      </div>
    </ThemeProvider>
  );
}

export default App;
